@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
.App {
  display: flex;
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
  flex-direction: column;
  text-align: center;
}
.start-title{
    font-size: xxx-large;
    margin-bottom: 5vh;
}

.start-button, .login-button, .logout-button {
    margin-top: 5vh;
    margin-left: 1.5vh;
    margin-right: 1.5vh;
    font-size: large;
    font-weight : bold;
    padding: 1vh 3vh 1vh 3vh;
    font-size: 18px;
    background-color: rgb(122, 195, 238);
    border: none;
    border-radius: 4px;
}
.login-title{
    font-size: xxx-large;
    margin-bottom: 5vh;
}

.signup-button {
    margin-top: 5vh;
    margin-left: 1.5vh;
    margin-right: 1.5vh;
    font-size: large;
    font-weight : bold;
    padding: 1vh 3vh 1vh 3vh;
}
.signup-title{
    font-size: xxx-large;
    margin-bottom: 5vh;
}

.register-button {
    margin-top: 5vh;
    margin-left: 1.5vh;
    margin-right: 1.5vh;
    font-size: large;
    font-weight : bold;
    padding: 1vh 3vh 1vh 3vh;
}
.Question-header {
    font-size: xx-large;
    margin-bottom: 1vh;
}
.display-board {
    text-align: center; 
    margin-left: 3vh;
    margin-right: 3vh;
    min-height: 300px;
    height: auto;
}

/* .hint {
    opacity:1;
    width:100px;
