    transition: width 0.5s, height 0.5s, opacity 0.5s 0.5s;
} */
.ans-button {
    padding: 3vh;
    font-size: medium;
}

/* .next-button {
    padding: 1vh;
    margin-top: 5vh;
    font-size: small;
} */
.question-timer{
    height: auto;
    width: 90px;
    margin: 1vh auto 3vh auto;
  }
.summary-title{
    font-size: xxx-large;
    margin-bottom: 5vh;
}

.total-score {
    font-size: x-large;
    font-weight : bold;
}

.restart-button, .home-button {
    margin-top: 5vh;
    margin-left: 1.5vh;
    margin-right: 1.5vh;
    font-size: large;
    font-weight : bold;
    padding: 1vh 3vh 1vh 3vh;
    background-color: rgb(122, 195, 238);
}
.input-field {
    width: 295px;
    height: 30px;
    font-size: 25px;
    border: rgb(122, 195, 238) 2px solid;
    margin-bottom: 10px;
    border-radius: 3px;
}

.input-field:focus {
    border: none;
}

label {
    display: block;
    font-size: 25px;
    margin-bottom: 2px;
    font-family: 'Courier New', Courier, monospace;
}

.custom-button {
    padding: 7px;
    margin-top: 5px;
    width: 300px;
    background-color: rgb(122, 195, 238);
    border: none;
    font-size: 18px;
    border-radius: 4px;
}

.small-custom-button {
    padding: 7px;
    margin-top: 5px;
    width: 100px;
    font-size: 18px;
    background-color: rgb(122, 195, 238);
    border: none;
    border-radius: 4px;
}

.verifier {
    text-align: center;
    display: inline-block;
    margin-top: 5px;
    margin-bottom: 5px;
}
/* import some good fonts */

* {
  margin: 0;
  padding: 0;
  font-family: "Montserrat", sans-serif;
}


body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

