.input-field {
    width: 295px;
    height: 30px;
    font-size: 25px;
    border: rgb(122, 195, 238) 2px solid;
    margin-bottom: 10px;
    border-radius: 3px;
}

.input-field:focus {
    border: none;
}

label {
    display: block;
    font-size: 25px;
    margin-bottom: 2px;
    font-family: 'Courier New', Courier, monospace;
}

.custom-button {
    padding: 7px;
    margin-top: 5px;
    width: 300px;
    background-color: rgb(122, 195, 238);
    border: none;
    font-size: 18px;
    border-radius: 4px;
}

.small-custom-button {
    padding: 7px;
    margin-top: 5px;
    width: 100px;
    font-size: 18px;
    background-color: rgb(122, 195, 238);
    border: none;
    border-radius: 4px;
}

.verifier {
    text-align: center;
    display: inline-block;
    margin-top: 5px;
    margin-bottom: 5px;
}